"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { setCookie } from "cookies-next";

export default function TopNavBar() {
  const [lang, setLang] = useState("en");
  const router = useRouter();

  useEffect(() => {
    // Initialize language from cookie or default
    // This is a placeholder, actual implementation may vary
    const cookieLang = document.cookie
      .split("; ")
      .find((row) => row.startsWith("lang="))
      ?.split("=")[1];
    if (cookieLang) setLang(cookieLang);
  }, []);

  return (
    <header className="bg-gradient-to-b from-[#CDEBFF] via-[#7DC8F7] to-[#39A3E6] p-4">
      {/* LANGUAGE TOGGLE */}
      <div className="flex justify-end mb-2">
        <div className="flex bg-white/30 backdrop-blur-sm rounded-full px-2 py-1 gap-1 text-white text-xs font-medium border border-white/40 shadow-sm">
          <button
            onClick={() => { setLang("so"); setCookie("lang", "so"); }}
            className={`px-2 py-0.5 rounded-full transition flex items-center justify-center ${
              lang === "so" ? "bg-white text-[#39A3E6] shadow" : "text-white/90"
            }`}
          >
            üá∏üá¥
          </button>
          <button
            onClick={() => { setLang("en"); setCookie("lang", "en"); }}
            className={`px-2 py-0.5 rounded-full transition flex items-center justify-center ${
              lang === "en" ? "bg-white text-[#39A3E6] shadow" : "text-white/90"
            }`}
          >
            üá¨üáß
          </button>
        </div>
      </div>

      <div className="flex items-center gap-2 bg-white rounded-full px-3 py-1 shadow-md border border-[#D5E7F4]">
        <button
          onClick={() => router.back()}
          className="h-10 w-10 rounded-full bg-[#39A3E6] text-white grid place-items-center shadow"
        >
          ‚Üê
        </button>
        {/* Additional content */}
      </div>
    </header>
  );
}